@import "../libs/include-media/dist/include-media";
@import "utils/special-version-variables";
@import "utils/functions";
@import "utils/mixins";
@import "utils/placeholders";

// variables
$displace: 1px;
// variables end

.spec-panel {
	display: none;
	position: relative;
	width: nth($spec-panel-size, 2);
	max-width: #{$spec-text-page};
	margin: 0 auto 20px;
	line-height: $spec-line-height--small;
	z-index: 995;
	box-sizing: border-box;
	border: 0 solid currentColor;
	border-width: 0 $spec-border-width $spec-border-width;
	// transition
	$prop: transform, box-shadow;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: 0.2s;
		timing-function: ease-in-out; }

	@include media('<tab-lg') {
		border-width: 0 0 $spec-border-width; }

	@include media('<mob-md') {
		margin-bottom: 10px; }

	&.settings-is-open {
		box-shadow: 0 0 0 4px currentColor;

		@include media('<tab-lg') {
			box-shadow: none; } }

	.#{$spec-mod} & {
		display: block; } }

.spec-panel__layout {
	overflow: hidden;
	overflow-y: hidden;
	overflow-x: hidden;
	box-sizing: border-box;
	@include spec-reset-bg;

	&:hover,
	.touchevents &,
	.no-js & {
		overflow-x: auto; } }

.spec-panel__holder {
	display: flex;
	justify-content: flex-start;
	box-sizing: border-box;
	padding: 0 round($spec-panel-inner-space / 2);

	@include media('<mob-md') {
		padding: 0 round($spec-panel-inner-space--sm / 2); } }

.spec-panel__layout,
.spec-panel__holder,
.spec-panel__item,
.spec-panel__footer {
	height: nth($spec-panel-size, 1);

	@include media('<mob-md') {
		height: nth($spec-panel-size--sm, 1); } }

.spec-panel__item, .spec-panel__footer {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 0 round($spec-panel-inner-space / 2);

	@include media('<mob-md') {
		padding: 0 round($spec-panel-inner-space--sm / 2); } }

.spec-panel__footer {
	margin-left: auto; }

.spec-panel__label {
	white-space: nowrap;
	padding-right: round($spec-panel-inner-space / 2);
	//font-family: Arial, Helvetica, sans-serif
	font-family: inherit;
	font-size: 18px;
	line-height: 22px;
	letter-spacing: normal;
	word-spacing: normal;
	word-break: normal;

	@include media('<tab-sm') {
		display: none; }

	//+media('<mob-md')
 }	//adding-right: round($spec-panel-inner-space--sm / 2)

.spec-panel__btn-group {
	display: flex;
	align-items: center;
	border-color: currentColor;
	margin-right: round($spec-panel-inner-space / 2);

	@include media('<mob-md') {
		margin-right: round($spec-panel-inner-space--sm / 2); }

	&:last-child {
		margin-right: 0; } }

.spec-panel__btn {
	position: relative;
	display: inline-block;
	vertical-align: top;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: $spec-panel__btn-size;
	height: $spec-panel__btn-size;
	margin-right: round($spec-panel-inner-space / 2);
	font-size: 18px;
	line-height: 22px;
	letter-spacing: normal;
	word-spacing: normal;
	border-radius: 4px;
	//border: 1px solid $spec-text-color
	border: 1px solid currentColor;
	box-sizing: border-box;
	// reset outline
	outline: none;
	-webkit-tap-highlight-color: rgba(0,0,0,0) !important;
	user-select: none !important;

	&, .#{$spec-mod} & {
		text-decoration: none;
		color: currentColor; }

	@include media('<mob-md') {
		width: $spec-panel__btn-size--sm;
		height: $spec-panel__btn-size--sm;
		margin-right: round($spec-panel-inner-space--sm / 2); }

	&:last-child {
		margin-right: 0; }

	& > span {
		display: none; }

	svg {
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		box-sizing: border-box;

		@include media('<mob-md') {
			transform: translate(-50%, -50%) scale(0.85); } }

	path {
		fill: currentColor; }

	// states of special buttons
	@include spec-button-states; }

.spec-panel__btn_color_def {
	&, .#{$spec-mod} & {
		color: $spec-text-color;
		background-color: $spec-bg-color; } }

.spec-panel__btn_color_white {
	&, .#{$spec-mod} & {
		color: $spec-text-color--scheme-wb;
		background-color: $spec-bg-color--scheme-wb; } }

.spec-panel__btn_color_blue {
	&, .#{$spec-mod} & {
		color: $spec-text-color--scheme-blue;
		background-color: $spec-bg-color--scheme-blue; } }

.spec-panel__btn_color_brown {
	&, .#{$spec-mod} & {
		color: $spec-text-color--scheme-brown;
		background-color: $spec-bg-color--scheme-brown; } }

.spec-panel__btn_color_green {
	&, .#{$spec-mod} & {
		color: $spec-text-color--scheme-green;
		background-color: $spec-bg-color--scheme-green; } }

$icon-spacing--size: 16px 2px; // h w
$icon-spacing--space: 3px; // h
.spec-panel__btn_spacing_sm, .spec-panel__btn_spacing_md, .spec-panel__btn_spacing_lg {
	.icon {
		&, &::before, &::after {
			display: block;
			position: absolute;
			left: 50%;
			top: 50%;
			height: nth($icon-spacing--size, 1);
			width: nth($icon-spacing--size, 2);
			background-color: currentColor; }

		& {
			margin: -#{round(nth($icon-spacing--size, 1)/2)} 0 0 -#{round(nth($icon-spacing--size, 2)/2)};

			@include media('<mob-md') {
				transform: scale(0.85); } }

		&::before, &::after {
			content: "";
			margin-top: -#{round(nth($icon-spacing--size, 1)/2)}; }

		&::before {
			margin-left: -#{$icon-spacing--space + round(nth($icon-spacing--size, 2)*1.5)}; }

		&::after {
			margin-left: #{$icon-spacing--space + round(nth($icon-spacing--size, 2)*0.5)}; } } }

.spec-panel__btn_spacing_md {
	.icon {
		$icon-spacing--space: 6px; // h

		&::before {
			margin-left: -#{$icon-spacing--space + round(nth($icon-spacing--size, 2)*1.5)}; }

		&::after {
			margin-left: #{$icon-spacing--space + round(nth($icon-spacing--size, 2)*0.5)}; } } }

.spec-panel__btn_spacing_lg {
	.icon {
		$icon-spacing--space: 9px; // h

		&::before {
			margin-left: -#{$icon-spacing--space + round(nth($icon-spacing--size, 2)*1.5)}; }

		&::after {
			margin-left: #{$icon-spacing--space + round(nth($icon-spacing--size, 2)*0.5)}; } } }

// Setting btn
.spec-panel__btn_settings, .spec-panel__btn_search {
	@include media('>=tab-lg') {
		width: auto;
		padding-left: 40px;
		padding-right: 10px;

		svg {
			left: 5px;
			transform: translate(0, -50%); }

		& > span {
			display: block; } } }

// Special Version Advanced Settings
.spec-drop {
	@include spec-reset-bg;
	overflow: hidden;
	display: none; }

.spec-drop__layout {
	padding: $spec-panel-inner-space;
	border-top: $spec-border-width solid currentColor;

	@include media('<mob-lg') {
		padding: $spec-panel-inner-space--sm; } }

.spec-drop__title {
	@include spec-font-size--title;
	//+font(36px, 48px)
	font-weight: bold;
	//letter-spacing: normal
	//word-spacing: normal
	word-wrap: break-word;
	margin-bottom: round($spec-panel-inner-space / 2);

	@include media('<mob-md') {
		margin-bottom: round($spec-panel-inner-space--sm / 2); }

	.spec-drop__item + & {
		margin-top: round($spec-panel-inner-space * 2); }

	@include media('<mob-md') {
		margin-bottom: round($spec-panel-inner-space--sm * 2); } }

.spec-drop__item, .spec-drop__footer {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: center;
	margin: round($spec-panel-inner-space / 2) 0 0;

	@include media('<mob-md') {
		margin: round($spec-panel-inner-space--sm / 2) 0 0; }

	.spec-drop__title + & {
		margin-top: 0; } }

.spec-drop__footer {
	margin-top: round($spec-panel-inner-space * 2);
	//margin-top: $spec-panel-inner-space
	padding-top: $spec-panel-inner-space;
	border-top: $spec-border-width solid currentColor;
	justify-content: flex-end;

	@include media('<mob-md') {
		margin-top: round($spec-panel-inner-space--sm * 2);
		padding-top: $spec-panel-inner-space--sm; } }

.spec-drop__label {
	display: inline-block; // if not support flex
	vertical-align: top; // if not support flex
	margin: round($spec-panel-inner-space / 2);
	margin-left: 0;
	@include spec-font-size--item;
	//+font(22px, 34px)
	//font-weight: bold
	//letter-spacing: normal
	//word-spacing: normal
	word-wrap: break-word;

	@include media('<mob-md') {
		margin: round($spec-panel-inner-space--sm / 2);
		margin-left: 0; } }

.spec-drop__btn-group {
	display: inline-block; // if not support flex
	vertical-align: top; // if not support flex
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: center;
	align-items: stretch;
	margin-right: round($spec-panel-inner-space / 2);

	@include media('<mob-md') {
		margin-right: round($spec-panel-inner-space--sm / 2); }

	&:last-child {
		margin-right: 0; } }

.spec-drop__btn {
	position: relative;
	display: inline-block; // if not support flex
	vertical-align: top; // if not support flex
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin: round($spec-panel-inner-space / 2);
	margin-left: 0;
	padding: 0 0.5em;
	//+spec-font-size--normal
	//letter-spacing: normal
	//word-spacing: normal
	min-height: 50px;
	border-radius: 4px;
	box-sizing: border-box;
	border: 1px solid currentColor;
	// reset outline
	outline: none;
	-webkit-tap-highlight-color: rgba(0,0,0,0) !important;
	user-select: none !important;

	&, .#{$spec-mod} & {
		text-decoration: none;
		color: currentColor; }

	@include media('<mob-md') {
		margin: round($spec-panel-inner-space--sm / 2);
		margin-left: 0; }

	svg {
		//display: none
		box-sizing: border-box;
		margin-right: 5px; }

	path {
		fill: currentColor; }

	.icon {
		display: none; }

	// states of special buttons
	@include spec-button-states; }

