// Special Version Advanced Settings end

/*button switch special version */
.spec-btn-switcher {
	position: fixed;
	left: 10px;
	top: 10px;
	padding-left: 1.9em;
	//font-family: Arial, Helvetica, sans-serif
	font-family: 'lato-regular', sans-serif;
	font-size: 18px;
	line-height: 30px;
	letter-spacing: normal;
	color: inherit;
	font-style: normal;
	box-sizing: border-box;
	z-index: 9999;

	.#{$spec-mod} & {
		font-family: inherit;
		font-weight: bold; }

	body:not(.#{$spec-mod}) &.to_normal {
		display: none; }

	.#{$spec-mod} &:not(.to_normal) {
		display: none; }

	svg {
		@include size(1.4em);
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(0, -50%);
		box-sizing: border-box; }

	path {
		fill: currentColor; }

	// in header
	.header & {
		position: absolute;
		left: auto;
		right: 15px;
		top: 15px;

		@include media('<tab-lg') {
			right: 55px;
			top: 5px;
			color: white; }

		.home-page & {
			color: white; }

		span {
			@include media('<tab-lg') {
				display: inline-block;
				vertical-align: top;
				font-size: 14px; }

			@include media('<mob-lg') {
				//width: 110px
				//font-size: 14px
				//line-height: 16px
				width: 95px;
				font-size: 13px;
				line-height: 15px; } }

		&:not(.to_normal) {
			@include media('<559px') {
				right: 40px;
				top: 0;
				height: 40px;
				width: 40px;
				padding-left: 0;
				box-shadow: inset 1px 0 0 rgba(white, 0.35); }

			@include media('<mob-md') {
				right: 30px; }

			svg {
				@include media('<559px') {
					left: 50%;
					transform: translate(-50%, -50%); } }

			span {
				@include media('<559px') {
					display: none; } } }

		.#{$spec-mod} & {
			color: currentColor;
			top: auto;
			right: 0;
			bottom: 60px;

			@include media('<tab-lg') {
				top: auto;
				bottom: 24px;
				right: 0; }

			@include media('<mob-lg') {
				order: 4;
				position: relative;
				top: auto;
				bottom: auto;
				right: auto;
				align-self: self-start; }

			span {
				width: auto;
				line-height: inherit; } } }

	// in footer
	.footer & {
		position: absolute;
		left: auto;
		top: auto;
		bottom: 10px;
		color: rgba(white, 0.8);

		.#{$spec-mod} & {
			color: currentColor;
			position: relative;
			bottom: auto; } } }

/*button switch special version end */

